import React from 'react';
import { Button, Paper, TextField, Typography, withStyles } from '@material-ui/core';

import { getPasswordImageForMode } from '../utils/resource-loader';
export const ID = {id: 'PASSWORD', title: 'Enter Prompt'};

const styles = theme => ({
  root: {
    display: 'flex',
    flexGrow: 1,
    margin: 40
  },
  button: {
    margin: theme.spacing(1),
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  vbox: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  hbox: {
    display: 'flex',
    flexDirection: 'row'
  },
  paperContainer: {
    minWidth: 300,
    height: 200,
    padding: 20
  },
  image: {
    height: 200
  }
});

class Password extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      password: ''
    };
  }
  handleChange = event => {
    this.setState({password: event.target.value});
  }
  submitPassword = () => {
    const { setPassword } = this.props;
    setPassword(this.state.password);
    this.setState({password: ''});
  }
  render() {
    const { classes, state: { mode } = {} } = this.props;

    return <div className={classes.root}>
        <div className={classes.hbox}>
          <Paper className={classes.paperContainer}>
            <div className={classes.vbox}>
              <Typography variant='h6'>
                Please sign here
              </Typography>

              <TextField
              id='password'
              label='Password'
              type='password'
              className={classes.textField}
              value={this.state.password}
              onChange={this.handleChange}
              onKeyPress={ev => {
                if (ev.key === 'Enter') {
                  this.submitPassword();
                }
              }}
              margin='normal'
              variant='outlined'
              />
            
              <Button color='primary' className={classes.button} onClick={() => this.submitPassword()} value='submit'>
                Login
              </Button>
            </div>
          </Paper>
          <img src={getPasswordImageForMode(mode)} alt='delivery' className={classes.image} />
        </div>
        
        {/* </div>
      </div> */}
    </div>;
  }
}

export default withStyles(styles, { withTheme: true })(Password);