import React from 'react';
import { withStyles } from '@material-ui/core';
import { Divider, List, ListSubheader, ListItem, ListItemIcon, ListItemText } from '@material-ui/core';

import KeyIcon from '@material-ui/icons/VpnKey';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import { getExerciseResources } from '../utils/resource-loader';
import { getExerciseIndexForPassword } from '../utils/password';

const styles = theme => ({
});

class DrawerContent extends React.Component {
  render() {
    const { state: { unlockables = {}, mode = {}} = {} } = this.props;
    const { shop: shopUnlocked = false, password } = unlockables; // TODO

    const exercises = getExerciseResources(mode);
    const unlockedIndex = getExerciseIndexForPassword({password, exercises});

    return (
    <div>
      <List subheader={<ListSubheader component='div'>Exercises</ListSubheader>}>
        {exercises.map(({title}, index) => index <= unlockedIndex ? (
          <List key={title}>
            <ListItem button onClick={() => this.props.navigateExercise(title)}>
              <ListItemIcon><KeyboardArrowRightIcon /></ListItemIcon>
              <ListItemText primary={title} />
            </ListItem>          
          </List>
        ) : undefined)}
      </List>
      <Divider />
      {/* {
        !extraExercises || extraExercises.length === 0 ? 
        undefined
        : (
        <>
          <List subheader={<ListSubheader component='div'>Extra Exercises</ListSubheader>}>
            {extraExercises.map(({title, description, exercise}, index) => (
              <Exercise title={title} key={`${title}-${index}`} onClick={() => this.viewExercise({title, exercise, description})}/>
            ))}
          </List>
          <Divider />
        </>
        )
      } */}
      {
        !shopUnlocked ? undefined : (
        <List>
          <ListItem button onClick={this.props.navigateShop}>
            <ListItemIcon><ShoppingCartIcon /></ListItemIcon>
            <ListItemText primary='Shop' />
          </ListItem>          
        </List>
        )
      }
      <List>
        <ListItem button onClick={this.props.navigatePassword}>
          <ListItemIcon><KeyIcon /></ListItemIcon>
          <ListItemText primary='Enter Prompt' />
        </ListItem>          
      </List>
    </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(DrawerContent);