import React from 'react';
import { AppBar, Toolbar, Typography, IconButton, withStyles } from '@material-ui/core';
import HomeIcon from '@material-ui/icons/Home';
import SettingsIcon from '@material-ui/icons/Settings';
import { getTitleForLevel, getTitleForText } from '../resources/exercises';

const styles = theme => ({
  homeButton: {
    marginRight: 20,
  },
  grow: {
    flexGrow: 1
  }
});

class AppToolbar extends React.Component {
  navigateHome = () => {
    this.props.navigateHome();
  }
  showChangeModePrompt = () => {
    this.props.showChangeModePrompt(true);
  }
  render() {
    const { classes, state: { navigation = {}, mode = {} } = {} } = this.props;
    const { currentView: { title } = {} } = navigation;
    const { selectedLevel, selectedText } = mode;
    return (
      <AppBar position='relative'>
        <Toolbar>
          <IconButton
            color='inherit'
            aria-label='Home'
            onClick={this.navigateHome}
            className={classes.homeButton}
          >
            <HomeIcon />
          </IconButton>
          <Typography variant='h6' color='inherit' noWrap className={classes.grow}>
            {title}
          </Typography>
          {!!selectedText && !!selectedLevel ?
            <Typography variant='subtitle1' color='inherit' noWrap >
              Level: {getTitleForLevel(selectedLevel)} Text: {getTitleForText({text: selectedText, level: selectedLevel})}
            </Typography>
            : undefined
          }
          <IconButton
            color='inherit'
            aria-label='Mode Selector'
            onClick={this.showChangeModePrompt}
          >
            <SettingsIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
    );
  }
}

export default withStyles(styles, { withTheme: true })(AppToolbar);