import React from 'react';
import { Typography, withStyles } from '@material-ui/core';
import Mission from './Mission';
import Assistance from './Assistance';


const styles = theme => ({
  summary: {
    margin: theme.spacing(1)
  },
  assistance: {
    marginTop: 40
  }
});
class Translation extends React.Component {
  render() {
    const { classes, missions, summary, assistance } = this.props;
    return (
      <div>
        <Typography className={classes.summary}>{summary}</Typography>
        {missions.map(({description, points, exercise, compulsory}, index) => <Mission key={index} points={points} description={description} exercise={exercise} compulsory={compulsory} index={index} />)}
        {!!assistance ? <Assistance assistance={assistance} className={classes.assistance} /> : undefined}
      </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Translation);