import React from 'react';
import { withStyles } from '@material-ui/core';
import { getView } from '../views';

const styles = theme => ({
  root: {
    margin: 16,
    display: 'flex',
    flexGrow: 1
  }
})

class Content extends React.Component {
  render() {
    const { classes, setPassword, state: { navigation = {}, mode = {} } = {} } = this.props;
    const View = getView(navigation.currentView);
    return (
      <div className={classes.root}>
        <View setPassword={setPassword} state={{mode, navigation}} />
      </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Content);