import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core';
import { CssBaseline } from '@material-ui/core';
import { Toolbar, Drawer, Content, Settings } from './components';
import { createStateApi, getState } from './utils/state-api';

const styles = theme => ({
  root: {
    display: 'flex',
    height: '100%',
    width: '100%'
  },
  hbox: {
    display: 'flex',
    flexDirection: 'row',
    flexGrow: 1
  },
  vbox: {
    display: 'flex',
    flexDirection: 'column',
    flexGrow: 1
  }
});

class App extends React.Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    store$: PropTypes.object.isRequired,
    applyCommand: PropTypes.func.isRequired
  };
  constructor(props) {
    super(props);
    this.state = {};
    this.subscription = undefined;
  }
  componentDidMount() {
    this.subscription = this.props.store$.subscribe(state => {
      this.setState({...state, ready: true});
    });
  }
  componentWillUnmount() {
    if (!!this.subscription) {
      this.subscription.unsubscribe();
      this.subscription = undefined;
    }
  }

  render() {
    if (!this.state.ready) {
      return <div></div>;
    }
    const { classes, applyCommand, applyState, changeMode } = this.props;
    const { navigation = {}, unlockables = {}, mode = {} } = getState(this.state);

    const api = createStateApi({state: this.state, applyCommand, applyState, changeMode});

    const { 
      navigateHome,
      showChangeModePrompt,
      navigateShop,
      navigatePassword,
      navigateExercise,
      setPassword,
      setMode
    } = api;

    return (
    <div className={classes.root}>
      <CssBaseline />
      <div className={classes.vbox}>
        <Toolbar state={{navigation, mode}} navigateHome={navigateHome} showChangeModePrompt={showChangeModePrompt} />
        <div className={classes.hbox}>
          <Drawer state={{unlockables, mode}} navigateShop={navigateShop} navigatePassword={navigatePassword} navigateExercise={navigateExercise} />
          <Content state={{navigation, mode}} setPassword={setPassword} />
        </div>
      </div>
      <Settings {...mode} setMode={setMode} showChangeModePrompt={showChangeModePrompt} />
    </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(App);
