import React from 'react';
import { withStyles } from '@material-ui/core';
import Mission from './Mission';



const styles = theme => ({
  root: {
    flexGrow: 1
  }
});
class Vocabulary extends React.Component {
  render() {
    const { classes, missions } = this.props;
    return (
      <div className={classes.root}>
        {missions.map(({description, points, exercise}, index) => <Mission key={index} points={points} description={description} exercise={exercise} index={index} />)}
      </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Vocabulary);